.body {
  /* background-color: #fff; */
  border-top: 10px solid #e4e4e4;
  padding-bottom: 50px;
}

.header {
  padding: 50px 0 20px 0;
  border-bottom: 5px double #e9e9e9;
}

.title {
  font-size: 49px;
  text-transform: uppercase;
  text-shadow: 5px 5px 5px #e9e9e9;
}

.logo {
  text-transform: uppercase;
  font-size: 24px;
  font-weight: 600;
  color: #8c4f00d3;
}

.description {
  font-size: 20px;
}

.subtitle {
  margin-top: 100px;
  font-size: 35px;
}

.contents {
  display: flex;
}

.content {
  position: relative;
  width: 400px;
  height: 300px;
  margin-right: 5px;
}

.img {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: cover;
  z-index: -9;
  transition: all 1s ease;
}

.comment {
  position: absolute;
  color: #fff;
  text-align: center;
}
.box1 {
  margin: 60px auto 0;
}
.box2 {
  margin: 200px 150px 0;
}

.intro {
  position: relative;
  height: 500px;
}

.background {
  background: rgba(0, 0, 0, 0.3);
}

.content:hover > .img,
.intro:hover > .img {
  scale: 1.1;
}
