.body {
  background-color: #fff;
  border-top: 10px solid #e4e4e4;
}

.content {
  padding: 65px 10px 20px;
  text-align: center;
}

.header {
  padding: 20px 40px 50px;
}

.title {
  margin: 10px auto 20px;
  text-align: center;
  font-size: 49px;
  text-transform: uppercase;
  text-shadow: 5px 5px 5px #e9e9e9;
}

/* form */
.form {
  display: flex;
  height: 40px;
  box-shadow: 5px 5px 5px #e4e4e4;
}

.form input {
  flex: 1 1;
  padding: 12px 40px;
  font-size: 14px;
  border: none;
  border-radius: 5px 0 0 5px;
  outline: none;
}

.form input::placeholder {
  color: #878787;
}
.form input:hover {
  color: #494949;
}

.form button[type="submit"] {
  flex: none;
  padding: 8px 10px;
  border-radius: 0 5px 5px 0;
  border: none;
  outline: none;
  color: #fff;
  background: #494949;
}

.form button[type="submit"]:hover {
  cursor: pointer;
}

.icon {
  width: 20px;
  height: 20px;
}

/* form -- End */

.topBtn {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 50px;
  height: 50px;
  font-weight: 800;
  border-radius: 50px;
  border: 1px solid #e9e9e9;
  background: transparent;
  margin: 20px;
}

.topBtn:hover {
  cursor: pointer;
  background: #494949;
  color: #fff;
  transition: all 0.8s ease;
}
