.container {
  margin: 120px auto;
  text-align: center;
}

.section {
  width: 500px;
  margin: 0 auto;
}

.header {
  text-align: left;
  padding: 1px 20px;
  background: gray;
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  border-radius: 8px 8px 0 0;
}

.sectionContainer {
  padding: 20px 10px;
  border: 1px solid #ededed;
  border-radius: 0 0 5px 5px;
  box-shadow: 1px 1px 5px #e4e4e4;
}

.loginForm {
  margin: 0;
  padding: 0;
}

.input > input {
  width: 100%;
  height: 50px;
  padding-left: 20px;
  border: 1px solid #ededed;
  margin-bottom: 10px;
  border-radius: 5px;
}

input:hover {
  border: 1px solid #c6c6c6;
}

.buttonBox {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 6px;
}

.buttonBox > [type="submit"] {
  margin-right: 10px;
}

.disabled {
  border: 0;
  padding: 0;
  margin-right: 10px;
  background: transparent;
  color: #fff;
  font-size: 15px;
  font-weight: 600;
}
.disabled:hover {
  cursor: pointer;
}
