.bg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  background: linear-gradient(
    135deg,
    #f9f9f9 48%,
    #8c3c0bd4 48%,
    #8c3c0bd4 78%,
    #f9f9f9 78%
  );
}

.container {
  display: flex;
  margin: 120px auto 226px;
}

.texts {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  justify-content: center;
  margin-right: 100px;
}

.logo {
  text-transform: uppercase;
  font-size: 24px;
  font-weight: 600;
  transition: all 1s ease;
  color: #8c4f00d3;
}

.logo:hover {
  color: #8c4f00;
  font-size: 30px;
}

.heading {
  margin: 15px 0;
  font-weight: 500;
  line-height: 1.39;
}

.description {
  margin: 15px 0 25px;
  font-size: 18px;
  line-height: 1.61;
  color: #878787;
  text-align: left;
  letter-spacing: normal;
}

.heading:hover,
.description:hover {
  cursor: default;
}

