ul li {
  list-style: none;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 134px;
  padding: 20px 20px 30px;
  background-color: #f9f9f9;
  border-top: solid 1px #ebebeb;
}

.cardSummary {
  padding: 10px;
}
.title {
  margin: 0 0 10px;
  overflow: hidden;
  font-size: 14px;
  font-weight: 500;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.details {
  width: 450px;
  margin: 0 auto;
}

.listName {
  float: left;
  width: 100px;
}

.wishListSummary {
  display: flex;
}

.wishListSummary > h2 {
  margin: 0 auto;
}

.delete {
  width: 30px;
  height: 30px;
  background-size: cover;
}
