.body {
  border-top: 10px solid #e4e4e4;
  padding-bottom: 50px;
  background: #fff;
}

.header {
  padding: 20px 40px;
  text-align: center;
}

.title {
  margin: 60px auto 30px;
  text-align: center;
  font-size: 49px;
  text-transform: uppercase;
  text-shadow: 5px 5px 5px #e9e9e9;
}

.subtitle {
  font-size: 30px;
}

.summary {
  color: #8f8f8f;
  white-space: pre-wrap;
}

.topBtn {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 50px;
  height: 50px;
  font-weight: 800;
  border-radius: 50px;
  border: 1px solid #e9e9e9;
  background: transparent;
  margin: 20px;
}

.topBtn:hover {
  cursor: pointer;
  background: #494949;
  color: #fff;
  transition: all 0.8s ease;
}
