.button {
  position: relative;
  padding: 13px 41px;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  background: rgba(73, 73, 73, 0.8);
  border: none;
  border-radius: 5px;
  outline: none;
  transition: all 0.5s ease;
}

div.button {
  display: inline-block;
}

.button.round {
  padding: 9px 22px;
  font-size: 14px;
  font-weight: 400;
  color: #fff;
  background-color: #494949;
  border-radius: 9999px;
}

.button:hover {
  background: rgb(73, 73, 73);
}
